import React, { memo } from 'react'
import styled from 'styled-components'
import { useTranslation } from 'react-i18next'
import Language_icon_en from '@/assets/language_en.png'
import Language_icon_cn from '@/assets/language_cn.png'
import { Toast } from 'antd-mobile'

const StyledButton = styled.button`
  position: absolute;
  right: 60px;
  top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  white-space: nowrap;
  width: 32px;
  height: 32px;
  border-radius: 10px;
  z-index: 99;
  background: transparent;
  @media (min-width:769px){
    right: calc(50% - 3.266665rem) !important;
  }
`

const LanguageIcon = styled.img`
  width: 34px;
  height: 30px;
`

export interface PropsType {
  right?: number
}

export default memo(function SetLanguage(props: PropsType) {
  const { right = 60 } = props

  const { i18n, t } = useTranslation()
  const langClick = () => {
    if (i18n.language === 'zh') i18n.changeLanguage('en')
    else i18n.changeLanguage('zh')
    Toast.show({
      content: t('switch.language'),
    })
  }

  return (
    <StyledButton onClick={langClick} style={{ right: right + 'px' }}>
      <LanguageIcon src={i18n.language === 'zh' ?Language_icon_cn:Language_icon_en} />
    </StyledButton>
  )
})
