/* eslint-disable */
import React, { memo, useEffect, useState } from 'react'
import { Tabs, Mask, Loading, Image } from 'antd-mobile'
import {
  DashboardWrapper,
  TabsWrapper,
  ContentTipsDome,
  BackWrapper,
} from './style'
import PurchaseDome from './PurchaseDome'
import SharesDome from './SharesDome'
import QRCodeDome from './QRCodeDome'
import AwardDome from './AwardDome'
import HandlingFeeDome from './HandlingFeeDome'
import IntroductionDome from './IntroductionDome'
import ContactUsDome from './ContactUsDome'
import BriefIntroductionDome from './BriefIntroductionDome'
import VideoDome from './VideoDome'
import PartnerDome from './PartnerDome'
import { throttle } from '@/utils'
import $ from 'jquery'
import { GamefoxInfinity_ABI, GamefoxInfinity_Address } from '@/contracts/constant'
import { SetUser } from '@/store/user/action'
import { useSelector, useDispatch } from 'react-redux'
import LOGO_TITLE from '@/assets/logo_title.png'
import MENU from '@/assets/menu.png'
import MenuList from '@/components/MenuList'
import SetLanguage from '@/components/SetLanguage'
import { useInterval } from '@/utils'

export default memo(function DashboardPage() {
  const tronWeb = useSelector((state: any) => state.tron.tronWeb)
  const ContractMain = tronWeb.contract(GamefoxInfinity_ABI, GamefoxInfinity_Address)

  // const [active, setActive] = useState('purchase')
  const [transparency, setTransparency] = useState(0)
  const dispatch = useDispatch()

  const myAddress = useSelector((state: any) => state.userInfo.address)

  const [isMask, setIsMask] = useState(false)
  const [isMaskText, setIsMaskText] = useState('')

  const [sendRefresh, setSendRefresh] = useState(true)

  const [isMenuMask, setIsMenuMask] = useState(false)

  const [times, setTimes] = useState<any>(null)

  useEffect(() => {
    if (!sendRefresh) {
      setTimeout(() => {
        setSendRefresh(true)
      }, 600)
    }
  }, [sendRefresh])

  useEffect(() => {
    if (myAddress) {
      getUserInfo(myAddress)
    }
  }, [myAddress])

  useInterval(() => {
    if (myAddress) {
      getUserInfo(myAddress)
    }
  }, times)

  const getUserInfo = async (address: string) => {
    try {
      let userInfo = await ContractMain.users(address).call()
      localStorage.setItem('userInfo', JSON.stringify(userInfo))
      dispatch(SetUser(userInfo))
      if (times === null) setTimes(3000)
    } catch (error) {}
  }

  /** 滚动条监听 */
  const HandleScroll = () => {
    let scrollTop1 = $('#tops').offset()?.top as number
    let number = Number(parseInt(String(scrollTop1).replace(/-/g, '')))
    if (number < 80) {
      setTransparency(0)
      return false
    }
    setTransparency(number > 100 ? 1 : Number((number / 100).toFixed(2)))
  }

  return (
    <DashboardWrapper onScroll={throttle(HandleScroll, 50)}>
      <TabsWrapper
        style={{
          background: `rgba(36, 44, 79, ${transparency})`,
        }}
      >
        <Image src={LOGO_TITLE} width="239.5px" height="43px"></Image>
        <Image
          src={MENU}
          width="34px"
          height="30px"
          onClick={() => {
            setIsMenuMask(true)
          }}
        ></Image>
        <SetLanguage />
      </TabsWrapper>
      <h2 id="tops">
        <a href="#tops"></a>
      </h2>
      <BackWrapper />
      <h2 id="home">
        <a href="#home"></a>
      </h2>
      <BriefIntroductionDome />
      <VideoDome />
      <h2 id="purchase">
        <a href="#purchase"></a>
      </h2>
      <PurchaseDome
        setIsMask={setIsMask}
        setIsMaskText={setIsMaskText}
        sendRefresh={(s) => {
          setSendRefresh(s)
        }}
      />
      {sendRefresh && (
        <SharesDome
          sendRefresh={(s) => {
            setSendRefresh(s)
          }}
          setIsMask={setIsMask}
          setIsMaskText={setIsMaskText}
        />
      )}
      <h2 id="recommend">
        <a href="#recommend"></a>
      </h2>
      <QRCodeDome />
      {sendRefresh && (
        <AwardDome
          sendRefresh={(s) => {
            setSendRefresh(s)
          }}
          setIsMask={setIsMask}
          setIsMaskText={setIsMaskText}
        />
      )}
      <h2 id="node">
        <a href="#node"></a>
      </h2>
      {sendRefresh && (
        <HandlingFeeDome
          sendRefresh={(s) => {
            setSendRefresh(s)
          }}
          setIsMask={setIsMask}
          setIsMaskText={setIsMaskText}
        />
      )}
      <h2 id="introduction">
        <a href="#introduction"></a>
      </h2>
      <IntroductionDome />
      <h2 id="partner">
        <a href="#partner"></a>
      </h2>
      <PartnerDome />
      <h2 id="us">
        <a href="#us"></a>
      </h2>
      <ContactUsDome />
      <Mask visible={isMask}>
        <ContentTipsDome>
          <span>{isMaskText}</span>
          <Loading color="primary" />
        </ContentTipsDome>
      </Mask>
      <MenuList visible={isMenuMask} setVisible={setIsMenuMask} />
    </DashboardWrapper>
  )
})
