import React, { useMemo } from 'react'
import { useSelector } from 'react-redux'
import {
  css,
  DefaultTheme,
  ThemeProvider as StyledComponentsThemeProvider,
} from 'styled-components'
import { Colors } from './styled'

const MEDIA_WIDTHS = {
  upToExtraSmall: 500,
  upToSmall: 720,
  upToMedium: 960,
  upToLarge: 1280,
}

const mediaWidthTemplates: {
  [width in keyof typeof MEDIA_WIDTHS]: typeof css
} = Object.keys(MEDIA_WIDTHS).reduce((accumulator, size) => {
  ;(accumulator as any)[size] = (a: any, b: any, c: any) => css`
    @media (max-width: ${(MEDIA_WIDTHS as any)[size]}px) {
      ${css(a, b, c)}
    }
  `
  return accumulator
}, {}) as any

const white = '#FFFFFF'
const black = '#000000'
export function colors(darkMode: boolean): Colors {
  return {
    white,
    black,

    color1: darkMode ? '#7D84A3' : '#7D84A3',
    color2: darkMode ? '#B7BDDC' : '#B7BDDC',
    color3: darkMode ? '#868EA6' : '#868EA6',
    color4: darkMode ? '#F5B326' : '#F5B326',
    color5: darkMode ? '#C9CFEF' : '#C9CFEF',
    color6: darkMode ? '#F3F4F8' : '#F3F4F8',
    color7: darkMode ? '#8E95B6' : '#8E95B6',

    btnColor: darkMode ? '#D8DFE6' : '#D8DFE6',

    borderColor1: darkMode ? '#4F5A8F' : '#4F5A8F',
    borderColor2: darkMode ? '#556198' : '#556198',
    borderColor3: darkMode ? '#4B568A' : '#4B568A',

    primary1: darkMode ? '#242C4F' : '#242C4F',

    bg1: darkMode ? 'rgba(40, 46, 80, .3)' : 'rgba(40, 46, 80, .3)',
  }
}

export function theme(darkMode: boolean): DefaultTheme {
  return {
    ...colors(darkMode),
    grids: {
      sm: 8,
      md: 12,
      lg: 24,
    },

    mediaWidth: mediaWidthTemplates,

    height: document.documentElement.clientHeight || document.body.clientHeight,
  }
}

export function ThemeProvider(
  { children }: { children: React.ReactNode },
  props: any,
) {
  const thmemBoolean = useSelector((state: any) => state.themeInfo.themeBoolean)

  const darkMode = thmemBoolean

  const themeObject = useMemo(() => theme(darkMode), [darkMode])

  return (
    <StyledComponentsThemeProvider theme={themeObject}>
      {children}
    </StyledComponentsThemeProvider>
  )
}
