import { useRef, useEffect } from 'react'
/** 截取前几位和后几位，其余 */
export const formatStr = (a: number, b: number, str: string) => {
  if (str.length < 14) {
    return str
  }
  return (
    str.substring(0, a) +
    new Array(15).join('.') +
    str.substring(str.length - b, str.length)
  )
}

/** 接口地址前缀 */
export const baseURL = '/api'

/** 滚动 节流 */
export const throttle = (fn: Function, rateTime: number) => {
  let timer: any = null
  return (...args: any[]) => {
    if (!timer) {
      timer = setTimeout(() => {
        fn.apply(this, args)
        timer = null
      }, rateTime)
    }
  }
}

/** 防抖 搜索 */
export const debounced = (fn: any, debTime: number) => {
  let timer: any = null
  return (...args: any[]) => {
    if (timer) clearTimeout(timer)
    timer = setTimeout(() => {
      fn.apply(this, args)
      timer = null
    }, debTime)
  }
}

/** form 表单数据格式化 */
export const serializeObject = (res: any) => {
  let array = res.serializeArray()
  let arrayForm: any = {}
  array.forEach((element: any) => {
    arrayForm[element.name] = element.value
  })
  return arrayForm
}

/** hash 值返回值格式化 */
export const hashNumberFormat = (hash: string) => {
  if (hash.length <= 15) {
    return hash
  }
  return `${hash.slice(0, 15)}...`
}

/** 计时器 */
export const useInterval = (
  fn: () => void,
  delay: number | null | undefined,
  options?: {
    immediate?: boolean
  },
) => {
  const immediate = options?.immediate
  // 如果options传了参数就取他的值

  const timerRef = useRef<() => void>()
  // 传入ref的是一个函数

  timerRef.current = fn

  useEffect(() => {
    if (delay === undefined || delay == null) return

    if (immediate) {
      timerRef.current?.()
      // 是否初始渲染就启动计时器
    }
    const timer = setInterval(() => {
      timerRef.current?.()
    }, delay)
    return () => {
      clearInterval(timer)
    }
  }, [delay, immediate])
  // 有了delay变化的时候就会重新启动
  // delay:number 改变间隔，或者重新启动计时器
  // delay:undefiend 暂停

  // // 或者说每次里面调用了传入的函数，导致外面的发生变化，其实还是会走到这个函数，又会在执行，重新赋值timerRef,绑定新的函数，这里其实获得的是最新的值
}

export const getQueryString = (url: string) => {
  // eslint-disable-next-line
  let theRequest: any = new Object()
  let strs = []
  // eslint-disable-next-line
  if (url.indexOf('?') != -1) {
    var str = url.substr(1)
    strs = str.split('&')
    for (var i = 0; i < strs.length; i++) {
      theRequest[strs[i].split('=')[0]] = unescape(strs[i].split('=')[1])
    }
  }
  return theRequest
}
