export interface HandlingFeeDomeTypes {
  setIsMask: (s: boolean) => void
  setIsMaskText: (s: string) => void
  sendRefresh: (s: boolean) => void
}

export default memo(function HandlingFeeDomePage(props: HandlingFeeDomeTypes) {
  const { t, i18n } = useTranslation()
  const tronWeb = useSelector((state: any) => state.tron.tronWeb)
  const ContractToken = tronWeb.contract(GamefoxToken_ABI, GamefoxToken_Address)

  const [numberValue, setNumberValue] = useState('1')

  const myAddress = useSelector((state: any) => state.userInfo.address)

  const {
    lastRound,
    lastPrice,
    currentSupply,
    balanceOf,
    // maxPurchaseAmount,
    releasable,
    calculateFee,
  } = useHandlingFeeHooks(myAddress)
  const { setIsMask, setIsMaskText, sendRefresh } = props

  const { minWithdrawAmount } = useAmountHooks()

  const submitBuy = async () => {
    let number = Number(numberValue)
    if (number === 0) {
      Toast.show({
        content: t('dashboard.handling.tips.quantity'),
      })
      return false
    }

    // if (tronWeb.toSun(number) > maxPurchaseAmount) {
    //   Toast.show({
    //     content: `${t(
    //       'dashboard.handling.tips.min.quantity',
    //     )}${SunTurnTRX(maxPurchaseAmount as any)}`,
    //   })
    //   return false
    // }

    const balance = await tronWeb.trx.getBalance(myAddress)
    if (balance < number * lastPrice) {
      Toast.show({
        content: `${t('dashboard.purchase.balance')}${SunTurnTRX(
          balance as any,
        )} TRX`,
      })
      return false
    }

    setIsMask(true)
    setIsMaskText(t('dashboard.handling.by.loading'))

    try {
      let response = await ContractToken.purchase(tronWeb.toSun(number)).send({
        feeLimit: 100000000,
        callValue: number * lastPrice
      })
      console.log(response)
      let times = String(new Date().getTime())
      localStorage.setItem(`purchase_${times}`, response)
      Cookies.set(`purchase_${times}`, response)
      if (response) {
        setIsMask(false)
        setIsMaskText('')
        Toast.show({
          content: t('dashboard.handling.by.loading.success'),
        })
        sendRefresh(false)
      }
    } catch (error) {
      setIsMask(false)
      setIsMaskText('')
      Toast.show({
        content: String(error),
      })
      console.log(error)
    }
  }

  const submitFeeIncome = async () => {
    if (releasable < Number(minWithdrawAmount)) {
      Toast.show({
        content: `${t('dashboard.handling.min.withdraw.amount')}${SunTurnTRX(
          minWithdrawAmount as any,
        )} TRX`,
      })
      return false
    }

    setIsMask(true)
    setIsMaskText(t('dashboard.handling.receiving'))
    try {
      let response = await ContractToken.release(myAddress).send({
        feeLimit: 100000000
      })
      let times = String(new Date().getTime())
      console.log(response)
      localStorage.setItem(`FeeIncome_${times}`, response)
      Cookies.set(`FeeIncome_${times}`, response)
      if (response) {
        setIsMask(false)
        setIsMaskText('')
        Toast.show({
          content: t('dashboard.handling.receiving.success'),
        })
        sendRefresh(false)
      }
    } catch (error) {
      setIsMask(false)
      setIsMaskText('')
      Toast.show({
        content: String(error),
      })
      console.log(error)
    }
  }

  return (
    <HandlingFeeWrapper>
      <Fee>
        <HandlingFeeTitle>
          {t('dashboard.handling.fee.title')}(
          <span>
            {i18n.language === 'zh'
              ? `第${lastRound}期`
              : `Phase ${lastRound}.`}
          </span>
          )
        </HandlingFeeTitle>
        <HandlingFeeList>
          <HandlingFeeListInfo>
            <span>{t('dashboard.handling.fare')}</span>
            <span>{SunTurnTRX(lastPrice)} TRX</span>
          </HandlingFeeListInfo>
          <HandlingFeeListInfo>
            <span>{t('dashboard.handling.votes')}</span>
            <span>{SunTurnTRX(currentSupply)}</span>
          </HandlingFeeListInfo>
          <HandlingFeeListInfo>
            <span>{t('dashboard.handling.buy')}</span>
            <div className="inputs">
              <Input
                defaultValue={'1'}
                onChange={(event) => {
                  setNumberValue(event)
                }}
              />
              <span>{t('dashboard.handling.unit')}</span>
            </div>
          </HandlingFeeListInfo>
          <Button color="primary" className="btns" onClick={submitBuy}>
            {t('dashboard.handling.buy.submit')}
          </Button>
        </HandlingFeeList>
      </Fee>
      <Fee style={{ paddingBottom: '20px' }}>
        <ReceiveList>
          {/* <ReceiveListInfo>
            <span>累计收益(TRX)</span>
            <span>xxx</span>
          </ReceiveListInfo> */}
          {/* <ReceiveListInfo>
            <span>{t('dashboard.handling.revenue')}(TRX)</span>
            <span>{SunTurnTRX(rewardPerToken)}</span>
          </ReceiveListInfo> */}
          <ReceiveListInfo>
            <span>{t('dashboard.handling.hold')}: </span>
            <span>{SunTurnTRX(balanceOf)}</span>
          </ReceiveListInfo>
          <ReceiveListInfo>
            <span>{t('dashboard.handling.receivable')}: </span>
            <span>{SunTurnTRX(releasable)} TRX</span>
          </ReceiveListInfo>
        </ReceiveList>
        <ReceiveLins></ReceiveLins>
        <Submit onClick={submitFeeIncome}>
          {t('dashboard.handling.receive')}
        </Submit>
        <div
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <ServiceCharge>
            {t('dashboard.handling.fee')}
            {SunTurnTRX(calculateFee)} %
          </ServiceCharge>
        </div>
      </Fee>
    </HandlingFeeWrapper>
  )
})
