export interface SharesDomeTypes {
  setIsMask: (s: boolean) => void
  setIsMaskText: (s: string) => void
  sendRefresh: (s: boolean) => void
}

export default memo(function SharesDomePage(props: SharesDomeTypes) {
  const { t } = useTranslation()
  // const [listTable] = useState<listType[]>(LIST_INIT)
  const tronWeb = useSelector((state: any) => state.tron.tronWeb)
  const ContractMain = tronWeb.contract(GamefoxInfinity_ABI, GamefoxInfinity_Address)

  const { setIsMask, setIsMaskText, sendRefresh } = props

  const myAddress = useSelector((state: any) => state.userInfo.address)
  const userInfo = useSelector((state: any) => state.userInfo.user)

  let { userRewards, calculateFee } =
    useSharesHooks(myAddress)

  // const LineDome: React.FC<{ title: string }> = ({ title }) => (
  //   <LineDomePage>{title}</LineDomePage>
  // )

  const { minWithdrawAmount } = useAmountHooks()

  /** 领取分红收益 */
  const getReward = async () => {
    // console.log(userRewards.toString(), minWithdrawAmount.toString());
    if (userRewards < Number(minWithdrawAmount)) {
      Toast.show({
        content: `${t('dashboard.share.min.withdraw.amount')}${SunTurnTRX(
          minWithdrawAmount as any,
        )} TRX`,
      })
      return false
    }

    setIsMask(true)
    setIsMaskText(t('dashboard.share.receiving'))
    try {
      let response = await ContractMain.getReward().send({
        feeLimit: 100000000,
        // callValue: userRewards,
      })
      let times = String(new Date().getTime())
      localStorage.setItem(`receive_${times}`, response)
      Cookies.set(`receive_${times}`, response)
      console.log(response)
      if (response) {
        setIsMask(false)
        setIsMaskText('')
        Toast.show({
          content: t('dashboard.share.receiving.success'),
        })
        sendRefresh(false)
      }
    } catch (error) {
      setIsMask(false)
      setIsMaskText('')
      Toast.show({
        content: String(error),
      })
      console.log(error)
    }
  }

  return (
    <SharesWrapper>
      {/* <SharesAddress>{SunTurnTRX(totalSupply) / 2} TRX</SharesAddress> */}
      {/* <SharesSpan>{t('dashboard.share.title')}</SharesSpan> */}
      {/* <SharesLine /> */}
      <SharesData>
        <DataList>
          <DataTitle>{t('dashboard.share.user.reward')}: </DataTitle>
          <DataNumber>{SunTurnTRX(userRewards)} TRX</DataNumber>
        </DataList>
        <DataList>
          <DataTitle>{t('dashboard.share.user.shares')}</DataTitle>
          <DataNumber>
            {tronWeb.toBigNumber(userInfo.shares).toNumber()}
          </DataNumber>
        </DataList>
      </SharesData>
      <Button color="primary" className="btns" onClick={getReward}>
        <div className="title">{t('dashboard.share.calculate.fee')}</div>
        <span>
          ({t('dashboard.share.calculate.fee.title')}
          {SunTurnTRX(calculateFee)} %)
        </span>
      </Button>
      {/* <LineDome title="提取记录" /> */}
      {/* <SharesTable>
        <div className="lins"></div>
        <table>
          <thead>
            <tr>
              <th>哈希值</th>
              <th>数量(TRX)</th>
              <th>时间</th>
            </tr>
          </thead>
          <tbody>
            {listTable.map((item: listType, i: number) => {
              return (
                <tr key={i}>
                  <td>{hashNumberFormat(item.hash)}</td>
                  <td>{item.number}</td>
                  <td>{item.times}</td>
                </tr>
              )
            })}
          </tbody>
        </table>
      </SharesTable> */}
    </SharesWrapper>
  )
})
