import React, { memo } from 'react'
import styled from 'styled-components'
import { Button, Toast } from 'antd-mobile'
import { useSelector } from 'react-redux'
import { useAwardHooks } from '@/hooks/useAwardHooks'
import { GamefoxInfinity_ABI, GamefoxInfinity_Address } from '@/contracts/constant'
import Cookies from 'js-cookie'
import { useAmountHooks } from '@/hooks/useAmountHooks'
import { SunTurnTRX } from '@/contracts/constant'
import { useTranslation } from 'react-i18next'
import REINCOME_BACKGROUND from '@/assets/reincome_background.png'

const AwardWrapper = styled.div`
  position: relative;
  top: -30px;
`

const AwardContent = styled.div`
  background: url(${REINCOME_BACKGROUND}) no-repeat;
  background-size: 100% 100%;
  min-height: 100px;
  margin: 0 13px;
  margin-top: 73px;
  padding: 18px 15px;
  position: relative;
`
const AwardBtn = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 11px 12.5px;
  .adm-button {
    width: 100%;
    margin-top: 50px;
    margin-bottom: 20px;
    height: 60px;
    border-radius: 5px;
    color: ${({ theme }) => `${theme.btnColor}`};
    .title {
      margin-top: 5px;
      font-size: 17px;
      font-weight: bold;
      color: ${({ theme }) => `${theme.btnColor}`};
    }
    span {
      color: #89beff;
      font-size: 10px;
    }
  }
`

const AwardTitle = styled.div`
  font-size: 30px;
  font-weight: bold;
  color: #f5b326;
  text-align: center;
  margin-top: 21px;
`

const AwardH5 = styled.div`
  font-size: 12px;
  font-weight: bold;
  color: #868ea6;
  text-align: center;
  margin-bottom: 37px;
`

export interface AwardTypes {
  setIsMask: (s: boolean) => void
  setIsMaskText: (s: string) => void
  sendRefresh: (s: boolean) => void
}

export default memo(function AwardDomePage(props: AwardTypes) {
  const { t } = useTranslation()
  // const [listTable] = useState<listType[]>(LIST_INIT)
  const tronWeb = useSelector((state: any) => state.tron.tronWeb)
  const ContractMain = tronWeb.contract(GamefoxInfinity_ABI, GamefoxInfinity_Address)

  const { setIsMask, setIsMaskText, sendRefresh } = props

  const userInfo = useSelector((state: any) => state.userInfo.user)
  const myAddress = useSelector((state: any) => state.userInfo.address)

  const { calculateFee } = useAwardHooks(myAddress)
  const { minWithdrawAmount } = useAmountHooks()

  const submitInviteReward = async () => {
    if (tronWeb.toBigNumber(userInfo.inviteRewards) < minWithdrawAmount) {
      Toast.show({
        content: `${t('dashboard.award.min.withdraw.amount')}${SunTurnTRX(
          minWithdrawAmount as any,
        )} TRX`,
      })
      return false
    }

    setIsMask(true)
    setIsMaskText(t('dashboard.award.receiving'))
    try {
      let response = await ContractMain.getInviteReward().send({
        feeLimit: 100000000
      })
      let times = String(new Date().getTime())
      localStorage.setItem(`extension_${times}`, response)
      Cookies.set(`extension_${times}`, response)
      console.log(response)
      if (response) {
        setIsMask(false)
        setIsMaskText('')
        Toast.show({
          content: t('dashboard.award.receiving.success'),
        })
        sendRefresh(false)
      }
    } catch (error) {
      setIsMask(false)
      setIsMaskText('')
      Toast.show({
        content: String(error),
      })
      console.log(error)
    }
  }

  return (
    <AwardWrapper>
      <AwardContent>
        <AwardTitle>
          {SunTurnTRX(
            tronWeb.toBigNumber(userInfo.inviteRewards).toNumber(),
          )}{' '}
          TRX
        </AwardTitle>
        <AwardH5>{t('dashboard.award.title')}</AwardH5>
        <div style={{ borderBottom: '1px dotted #4C588F' }}></div>
        <AwardBtn>
          <Button color="primary" onClick={submitInviteReward}>
            <div className="title">{t('dashboard.award.extract')}</div>
            <span>
              ({t('dashboard.award.fee')}
              {SunTurnTRX(calculateFee)} %)
            </span>
          </Button>
        </AwardBtn>
      </AwardContent>
    </AwardWrapper>
  )
})
