import { useEffect, useState } from 'react'
import {
  GamefoxInfinity_ABI,
  GamefoxToken_ABI,
  GamefoxInfinity_Address,
  GamefoxToken_Address,
} from '@/contracts/constant'
import { useSelector } from 'react-redux'
import { useInterval } from '@/utils'

export function useHandlingFeeHooks(myAddress: string) {
  const tronWeb = useSelector((state: any) => state.tron.tronWeb)
  const ContractMain = tronWeb.contract(GamefoxInfinity_ABI, GamefoxInfinity_Address)
  const ContractToken = tronWeb.contract(GamefoxToken_ABI, GamefoxToken_Address)

  const [lastRound, setLastRound] = useState(1)
  const [lastPrice, setLastPrice] = useState(0)
  const [currentSupply, setCurrentSupply] = useState(0)
  const [balanceOf, setBalanceOf] = useState(0)
  // const [maxPurchaseAmount, setMaxPurchaseAmount] = useState(100000000)
  // const [rewardPerToken, setRewardPerToken] = useState(0)
  const [releasable, setReleasable] = useState(0)
  const [calculateFee, setCalculateFee] = useState(50000000)

  const [times, setTimes] = useState<any>(null)

  useEffect(() => {
    setTimeout(() => {
      getLastRound()
      // getLastPrice()
      // getCurrentSupply()
      getBalanceOf()
    }, 300)
    setTimeout(() => {
      // getMaxPurchaseAmount()
      // getRewardPerToken()
      if (myAddress) {
        getReleasable()
        getCalculateFee()
      }
    }, 600)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useInterval(() => {
    // getCurrentSupply()
    getBalanceOf()
    getReleasable()
  }, times)

  /** 获取当前轮数（第几期） */
  const getLastRound = async () => {
    try {
      let roundId = await ContractToken.lastRound().call()
      setLastRound(tronWeb.toBigNumber(roundId).toNumber())

      let rounds = await ContractToken.rounds(roundId).call()
      setLastPrice(tronWeb.toBigNumber(rounds.price).toNumber())
      setCurrentSupply(tronWeb.toBigNumber(rounds.supply.sub(rounds.sold).sub(1000000)).toNumber())
      if (times === null) setTimes(3000)
    } catch (error) {
      console.log(error)
    }
  }

  /** 获取当前轮的价格、票价 */
  // const getLastPrice = async () => {
  //   try {
  //     let response = await ContractToken.lastPrice().call()
  //     setLastPrice(tronWeb.toBigNumber(response).toNumber())
  //   } catch (error) {
  //     console.log(error)
  //   }
  // }

  /** 获取当前剩余的票数 */
  // const getCurrentSupply = async () => {
  //   try {
  //     let response = await ContractToken.balanceOf(GamefoxToken_Address).call()
  //     setCurrentSupply(tronWeb.toBigNumber(response).toNumber())
  //     if (times === null) setTimes(3000)
  //   } catch (error) {
  //     console.log(error)
  //   }
  // }

  /** 获取当前剩余的票数 */
  const getBalanceOf = async () => {
    try {
      let response = await ContractToken.balanceOf(myAddress).call()
      setBalanceOf(tronWeb.toBigNumber(response).toNumber())
      if (times === null) setTimes(3000)
    } catch (error) {
      console.log(error)
    }
  }

  /** 获取最大的购买数量 */
  // const getMaxPurchaseAmount = async () => {
  //   try {
  //     let response = await ContractToken.maxPurchaseAmount().call()
  //     setMaxPurchaseAmount(tronWeb.toBigNumber(response).toNumber())
  //   } catch (error) {
  //     console.log(error)
  //   }
  // }

  /** 获取每票收益  */
  // const getRewardPerToken = async () => {
  //   try {
  //     let response = await ContractToken.rewardPerToken().call()
  //     setRewardPerToken(tronWeb.toBigNumber(response).toNumber())
  //   } catch (error) {
  //     console.log(error)
  //   }
  // }

  /** 可领收益 */
  const getReleasable = async () => {
    try {
      let response = await ContractToken.releasable(myAddress).call()
      setReleasable(tronWeb.toBigNumber(response).toNumber())
      // getCalculateFee(tronWeb.toBigNumber(response).toNumber())
      if (times === null) setTimes(3000)
    } catch (error) {
      console.log(error)
    }
  }

  /** 计算手续费  -- 领取的手续费 */
  // const getCalculateFee = async (amount: any) => {
  const getCalculateFee = async () => {
    // if (amount === 0) {
    //   return false
    // }
    try {
      let response = await ContractMain.calculateFee(myAddress, 100000000).call()
      let number = tronWeb.toBigNumber(response).toNumber()
      setCalculateFee(number)
    } catch (error) {
      console.log(error)
    }
  }

  return {
    lastRound,
    lastPrice,
    currentSupply,
    balanceOf,
    // maxPurchaseAmount,
    releasable,
    calculateFee,
  }
}
