import { useEffect, useState } from 'react'
import { GamefoxInfinity_ABI, GamefoxInfinity_Address } from '@/contracts/constant'
import { useSelector } from 'react-redux'

export function useAmountHooks() {
  const tronWeb = useSelector((state: any) => state.tron.tronWeb)
  const ContractMain = tronWeb.contract(GamefoxInfinity_ABI, GamefoxInfinity_Address)

  const [minPurchaseAmount, setMinPurchaseAmount] = useState('1000000000')
  const [minWithdrawAmount, setMinWithdrawAmount] = useState('100000000')

  useEffect(() => {
    getMinPurchaseAmount()
    getMinWithdrawAmount()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  /** 获取最小购买金额 */
  const getMinPurchaseAmount = async () => {
    try {
      let response = await ContractMain.minPurchaseAmount().call()
      setMinPurchaseAmount(tronWeb.toBigNumber(response))
    } catch (error) {}
  }

  /** 获取最小提取金额 */
  const getMinWithdrawAmount = async () => {
    try {
      let response = await ContractMain.minWithdrawAmount().call()
      setMinWithdrawAmount(tronWeb.toBigNumber(response))
    } catch (error) {}
  }

  return { minPurchaseAmount, minWithdrawAmount }
}
