import { memo } from 'react'
import styled from 'styled-components'
import { Mask, Image } from 'antd-mobile'
import { TabsWrapper } from '@/pages/dashboard/style'
import LOGO_TITLE from '@/assets/logo_title.png'
import MENU_ACTIVE from '@/assets/menu_active.png'
import SetLanguage from '@/components/SetLanguage'
import { useTranslation } from 'react-i18next'

const MenuListWrapper = styled.div`
   @media (min-width:769px){
    max-width: 8.53333rem;
    margin-left: calc(50% - 4.266665rem);
  }
`

const MenuList = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: start;
  @media (min-width:769px){
    max-width: 8.53333rem;
    /* margin-left: calc(50% - 4.266665rem); */
  }
`

const MenuListInfo = styled.div`
  padding: 0 30px;
  position: relative;
  z-index: 9999;
  width: 100%;
  height: 44px;
  border-top: 1px solid #555555;
  background: #242c4f;
  display: flex;
  align-items: center;
  justify-content: space-between;
  a {
    color: #ffffff;
    font-size: 14px;
    width: 100%;
  }
`

export interface MenuListPageType {
  visible: boolean
  setVisible: (s: boolean) => void
}

export default memo(function MenuListDomePage(props: MenuListPageType) {
  const { t } = useTranslation()
  const { visible, setVisible } = props

  const TitleTabs = (text: string, key: string) => (
    <a href={`#${key}`}>{text}</a>
  )

  const closeMask = () => {
    setVisible(false)
  }

  return (
    <MenuListWrapper>
      <Mask
        style={{ zIndex: 9998 }}
        color="black"
        visible={visible}
        onMaskClick={closeMask}
      >
        <MenuList>
          <TabsWrapper style={{ position: 'static' }}>
            <Image src={LOGO_TITLE} width="239.5px" height="43px"></Image>
            <Image
              src={MENU_ACTIVE}
              width="34px"
              height="30px"
              onClick={closeMask}
            ></Image>
            <SetLanguage />
          </TabsWrapper>
          <MenuListInfo onClick={closeMask}>
            {TitleTabs(t('app.menu.home'), 'home')}
          </MenuListInfo>
          <MenuListInfo onClick={closeMask}>
            {TitleTabs(t('app.menu.purchase'), 'purchase')}
          </MenuListInfo>
          <MenuListInfo onClick={closeMask}>
            {TitleTabs(t('app.menu.recommend'), 'recommend')}
          </MenuListInfo>
          <MenuListInfo onClick={closeMask}>
            {TitleTabs(t('app.menu.node'), 'node')}
          </MenuListInfo>
          <MenuListInfo onClick={closeMask}>
            {TitleTabs(t('app.menu.introduction'), 'introduction')}
          </MenuListInfo>
          <MenuListInfo onClick={closeMask}>
            {TitleTabs(t('app.menu.partner'), 'partner')}
          </MenuListInfo>
          <MenuListInfo onClick={closeMask}>
            {TitleTabs(t('app.menu.us'), 'us')}
          </MenuListInfo>
        </MenuList>
      </Mask>
    </MenuListWrapper>
  )
})
