export default memo(function QRCodeDomePage() {
  // const [listTable] = useState<listType[]>(LIST_INIT)
  // const LineDome: React.FC<{ title: string }> = ({ title }) => (
  //   <LineDomePage>{title}</LineDomePage>
  // )
  const { t } = useTranslation()

  const myAddress = useSelector((state: any) => state.userInfo.address)
  const userInfo = useSelector((state: any) => state.userInfo.user)

  const copyAddress = () => {
    const range = document.createRange()
    range.selectNode(document.getElementById('copyVal') as any)
    const selection: any = window.getSelection()
    if (selection.rangeCount > 0) selection.removeAllRanges()
    selection.addRange(range)
    document.execCommand('copy')
    Toast.show({
      content: t('dashboard.qrcode.copy.success'),
    })
  }

  return (
    <QRCodeWrapper>
      <QRCodeTitle>{t('dashboard.qrcode.title')}</QRCodeTitle>
      <QRCodeContent>
        <QRCode
          value={`${QR_CODE_SHARE_URL}?p=${myAddress}`}
          style={{
            width: '170px',
            height: '170px',
            borderRadius: '5px',
          }}
        />
      </QRCodeContent>
      <div style={{ height: '11px' }}></div>
      <div style={{ textAlign: 'center' }}>
        <ContentTitle>
          {t('dashboard.qrcode.recommend')}
          <span>{userInfo.child}</span>
        </ContentTitle>
      </div>
      <div style={{ height: '23px' }}></div>
      <QRCodeLink>
        {t('dashboard.qrcode.link')}
        <span id="copyVal">{`${QR_CODE_SHARE_URL}?p=${myAddress}`}</span>
        <Button onClick={copyAddress}>{t('dashboard.qrcode.copy')}</Button>
      </QRCodeLink>
      {/* <LineDome title="推荐记录" /> */}
      {/* <QRCodeTable>
        <thead>
          <tr>
            <th>钱包地址</th>
            <th>时间</th>
          </tr>
        </thead>
        <tbody>
          {listTable.map((item: listType, i: number) => {
            return (
              <tr key={i}>
                <td>{formatStr(10, 8, item.address)}</td>
                <td>{item.times}</td>
              </tr>
            )
          })}
        </tbody>
      </QRCodeTable> */}
    </QRCodeWrapper>
  )
})
