import { useEffect, useState } from 'react'
import { GamefoxInfinity_ABI, GamefoxInfinity_Address } from '@/contracts/constant'
import { useSelector } from 'react-redux'

export interface propsType {
  myAddress: string
  amount: any
}

export function useAwardHooks(myAddress: string) {
  const tronWeb = useSelector((state: any) => state.tron.tronWeb)
  const ContractMain = tronWeb.contract(GamefoxInfinity_ABI, GamefoxInfinity_Address)

  const [calculateFee, setCalculateFee] = useState(50000000)

  useEffect(() => {
    setTimeout(() => {
      if (myAddress) {
        getCalculateFee()
      }
    }, 600)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [myAddress])

  /*** 计算奖励的手续费 */
  const getCalculateFee = async () => {
    try {
      let response = await ContractMain.calculateFee(myAddress, 100000000).call()
      let number = tronWeb.toBigNumber(response).toNumber()
      setCalculateFee(number)
    } catch (error) {
      console.log(error)
    }
  }
  return { calculateFee }
}
