import { useEffect, useState } from 'react'
import { GamefoxInfinity_ABI, GamefoxInfinity_Address } from '@/contracts/constant'
import { useSelector } from 'react-redux'
import { useInterval } from '@/utils'

export function useSharesHooks(myAddress: string) {
  const tronWeb = useSelector((state: any) => state.tron.tronWeb)
  const ContractMain = tronWeb.contract(GamefoxInfinity_ABI, GamefoxInfinity_Address)

  // const [totalSupply, setTotalSupply] = useState(0)
  const [totalShares, setTotalShares] = useState(0)
  const [userRewards, setUserRewards] = useState(0)
  const [calculateFee, setCalculateFee] = useState(50000000)

  const [times, setTimes] = useState<any>(null)

  useEffect(() => {
    setTimeout(() => {
      // getTotalSupply()
      getTotalShares()
      if (myAddress) {
        getUserAddressEarned()
        getCalculateFee()
      }
    }, 600)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [myAddress])

  useInterval(() => {
    // getTotalSupply()
    getUserAddressEarned()
  }, times)

  /** 获取分红总额 */
  // const getTotalSupply = async () => {
  //   try {
  //     let response = await ContractMain.totalSales().call()
  //     setTotalSupply(tronWeb.toBigNumber(response).toNumber())
  //     if (times === null) setTimes(3000)
  //   } catch (error) {
  //     console.log(error)
  //   }
  // }

  /** 获取分红权数量 */
  const getTotalShares = async () => {
    try {
      let response = await ContractMain.totalShares().call()
      setTotalShares(tronWeb.toBigNumber(response).toNumber())
    } catch (error) {
      console.log(error)
    }
  }

  /** 制定用户可领取分红收益 */
  const getUserAddressEarned = async () => {
    try {
      let response = await ContractMain.earned(myAddress).call()
      setUserRewards(tronWeb.toBigNumber(response).toNumber())
      // getCalculateFee(tronWeb.toBigNumber(response).toNumber())
      if (times === null) setTimes(3000)
    } catch (error) {
      console.log(error)
    }
  }

  /*** 计算奖励的手续费 */
  // const getCalculateFee = async (amount: number) => {
  const getCalculateFee = async () => {
    // if (amount === 0) {
    //   return false
    // }
    try {
      let response = await ContractMain.calculateFee(myAddress, 100000000).call()
      setCalculateFee(tronWeb.toBigNumber(response).toNumber())
    } catch (error) {
      console.log(error)
    }
  }
  return {
    // totalSupply,
    totalShares,
    userRewards,
    calculateFee
  }
}
