import * as actionTypes from './constants'

const initState = {
  tronWeb: {},
}

export default function reducer(state = initState, action: any) {
  const { type } = action
  switch (type) {
    case actionTypes.SAVE_TronWeb:
      return { ...state, tronWeb: action.tronWeb }
    default:
      return state
  }
}
