import React, { memo, useState } from 'react'
import styled from 'styled-components'
import { Image, Button, Toast } from 'antd-mobile'
import ICON1 from '@/assets/icon1.png'
import ICON2 from '@/assets/icon2.png'
import ICON3 from '@/assets/icon3.png'
import ICON4 from '@/assets/icon4.png'
import ICON_ACTIVE from '@/assets/icon_active.png'
import { GamefoxInfinity_ABI, GamefoxInfinity_Address } from '@/contracts/constant'
import { useSelector } from 'react-redux'
import Cookies from 'js-cookie'
import { useAmountHooks } from '@/hooks/useAmountHooks'
import { SunTurnTRX } from '@/contracts/constant'
import { useTranslation } from 'react-i18next'

const PurchaseWrapper = styled.div`
  position: relative;
  z-index: 3;
  margin: 11px 12.5px;
  padding: 22px 14px;
  border-radius: 15px;
  background: linear-gradient(180deg, #47538f, #2a3256);
  box-shadow: 0px 1.5px 13px 0px rgba(30, 32, 50, 0.3);
  .adm-button {
    width: 100%;
    margin-top: 50px;
    margin-bottom: 20px;
    height: 60px;
    border-radius: 5px;
    color: ${({ theme }) => `${theme.btnColor}`};
  }
`

const PurchaseSpan = styled.div`
  font-size: 15px;
  font-weight: 500;
  color: ${({ theme }) => `${theme.color2}`};
  padding-bottom: 21px;
  border-bottom: ${({ theme }) => `1px solid ${theme.borderColor1}`};
`

const PurchaseList = styled.div`
  display: flex;
  flex-wrap: wrap;
  .right {
    margin-right: calc(100% - 300px);
  }
  .active {
    background: #5b6bb8;
    span {
      color: ${({ theme }) => `${theme.white}`};
    }
  }
`

const PurchaseListInfo = styled.div`
  width: 150px;
  height: 60px;
  background: #f2f2f2;
  box-shadow: 0px 2px 6px 0px rgba(5, 12, 34, 0.19);
  border-radius: 10px;
  margin-top: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  span {
    color: ${({ theme }) => `${theme.color3}`};
    font-size: 14px;
    font-weight: bold;
  }
  .adm-image {
    width: 14px;
    height: 16px;
    margin-right: 17px;
  }
`

const TRX_Number_INIT = [1000, 5000, 10000, 20000]

export interface PurchaseDomeTypes {
  setIsMask: (s: boolean) => void
  setIsMaskText: (s: string) => void
  sendRefresh: (s: boolean) => void
}

export default memo(function PurchaseDomePage(props: PurchaseDomeTypes) {
  const { t } = useTranslation()

  const tronWeb = useSelector((state: any) => state.tron.tronWeb)
  const ContractMain = tronWeb.contract(GamefoxInfinity_ABI, GamefoxInfinity_Address)

  const [trxNumber] = useState<number[]>(TRX_Number_INIT)
  const [activeTRX, setActiveTRX] = useState<number>(0)
  const { setIsMask, setIsMaskText, sendRefresh } = props

  const userInfo = useSelector((state: any) => state.userInfo.user)
  const myAddress = useSelector((state: any) => state.userInfo.address)
  const parentAddress = useSelector(
    (state: any) => state.userInfo.parentAddress,
  )

  const { minPurchaseAmount } = useAmountHooks()

  const onSubmit = async () => {
    let callValue = trxNumber[activeTRX]

    if (tronWeb.toSun(callValue) < minPurchaseAmount) {
      Toast.show({
        content: `${t('dashboard.purchase.min.amount')}${SunTurnTRX(
          minPurchaseAmount as any,
        )}TRX`,
      })
      return false
    }

    const balance = await tronWeb.trx.getBalance(myAddress)
    if (balance < tronWeb.toSun(callValue)) {
      Toast.show({
        content: `${t('dashboard.purchase.balance')}${SunTurnTRX(
          balance as any,
        )}TRX`,
      })
      return false
    }

    setIsMask(true)
    setIsMaskText(t('dashboard.purchase.buy.loading'))
    try {
      let response = await ContractMain.purchase(
        tronWeb.toSun(callValue),
        parentAddress || userInfo.parent
      ).send({
        feeLimit: 100000000,
        callValue: tronWeb.toSun(callValue)
      })
      let times = String(new Date().getTime())
      localStorage.setItem(`buy_${times}`, response)
      Cookies.set(`buy_${times}`, response)
      console.log(response)
      if (response) {
        setIsMask(false)
        setIsMaskText('')
        Toast.show({
          content: t('dashboard.purchase.buy.success'),
        })
        sendRefresh(false)
      }
    } catch (error) {
      setIsMask(false)
      setIsMaskText('')
      Toast.show({
        content: String(error),
      })
      console.log(error)
    }
  }

  return (
    <PurchaseWrapper>
      <PurchaseSpan>{t('dashboard.purchase.title')}</PurchaseSpan>
      <PurchaseList>
        {trxNumber.map((item, i) => (
          <PurchaseListInfo
            key={i}
            className={
              activeTRX === i
                ? `${i % 2 === 0 ? 'right active' : 'active'}`
                : `${i % 2 === 0 ? 'right' : ''}`
            }
            onClick={() => {
              setActiveTRX(i)
            }}
          >
            {activeTRX !== i && (
              <>
                {i === 0 && <Image src={ICON1}></Image>}
                {i === 1 && <Image src={ICON2}></Image>}
                {i === 2 && <Image src={ICON3}></Image>}
                {i === 3 && <Image src={ICON4}></Image>}
              </>
            )}
            {activeTRX === i && <Image src={ICON_ACTIVE}></Image>}
            <span>{item} TRX</span>
          </PurchaseListInfo>
        ))}
      </PurchaseList>
      <Button color="primary" className="btns" onClick={onSubmit}>
        {t('dashboard.purchase.btn.submit')}
      </Button>
    </PurchaseWrapper>
  )
})
