import React, { memo, useEffect, useState } from 'react'
import styled from 'styled-components'
import { useDispatch } from 'react-redux'
import { renderRoutes } from 'react-router-config'
import routes from './router'
import type { requestAccountsResponse } from '@/utils/data.d'
import { Toast, Mask, Loading } from 'antd-mobile'
import { getTronAddress } from '@/contracts/tronLink'
import {
  tronRequestAccountsCodeMsg,
  tronRequestAccountsCodeMsgEn,
} from '@/contracts/constant'
import Login from './components/Login'
import { SaveAddress, SaveParentAddress } from '@/store/user/action'
import { SaveTronWeb } from '@/store/tronWeb/action'
import { getQueryString, useInterval } from '@/utils'
import Cookies from 'js-cookie'
import { useTranslation } from 'react-i18next'

const AppWrapper = styled.div`
  background: ${({ theme }) => `${theme.primary1}`};
  width: 100%;
  min-height: 100vh;
  position: relative;
  .adm-mask-content {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  @media (min-width:769px){
    max-width: 8.53333rem;
    margin: 0 auto !important;
  }
`

const MaskContent = styled.div`
  min-width: 100px;
  height: 100px;
  position: relative;
  z-index: 999;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-self: center;
  background: #fff;
  font-size: 14px;
  font-weight: bold;
  .loadings {
    display: flex;
    justify-content: center;
    align-items: center;
  }
`

declare const window: any

export default memo(function App(props) {
  const { t, i18n } = useTranslation()

  const [userCode, setUserCode] = useState(0)
  const [visible, setVisible] = useState(true)
  const [isLogin, setIsLogin] = useState(false)

  const [loadingText, setLoadingText] = useState(t('app.loading'))

  const dispatch = useDispatch()

  const [time, setTime] = useState<any>(3000)

  /** 此次判断用于检测用户是否授权状态 */
  const [times, setTimes] = useState<any>(1000)

  useInterval(async () => {
    if (!window.tronWeb) {
      return false
    }
    if (!window.tronLink) {
      return false
    }
    if (window.tronLink.ready && userCode === 0) {
      dispatch(SaveTronWeb(window.tronLink.tronWeb))
      Cookies.set('tronWeb_s', window.tronLink.tronWeb)
      // 登录成功回调此页面。并保留朱tronWeb信息，并存入缓存，以方便读取， ----- 防止页面刷新报错
      window.location.reload()
    }
  }, time)

  /**
   * 检测用户授权后是否取消授权：一旦用户授权开启检测；若用户未授权 关闭操作
   */
  useInterval(async () => {
    if (!window.tronWeb) {
      return false
    }
    if (!window.tronLink) {
      return false
    }
    if (!window.tronLink.ready && userCode !== 0) {
      setUserCode(4001)
      setIsLogin(true)
      setTimes(null)
    }
  }, times)

  useEffect(() => {
    setTimeout(() => {
      userAuthorizedOrNot()
    }, 100)
    if (userCode !== 0 && userCode !== 200) {
      // 用户未授权重新获取授权
      getTronAddress()
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    let objSearch = getQueryString(window.location.search)
    if (objSearch.p !== undefined) {
      let p = objSearch.p || '410000000000000000000000000000000000000000'
      dispatch(SaveParentAddress(p))
      Cookies.set('parentAddress', p)
      localStorage.setItem('parentAddress', p)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props])

  useEffect(() => {
    if (userCode !== 0) setVisible(false)
    if (userCode !== 4001) {
      setIsLogin(false)
    }
    if (userCode === 4001) {
      setIsLogin(true)
    }
    if (userCode === 200) {
      // 授权成功、获取用户地址和用户信息
      let address = window.tronWeb.defaultAddress.base58
      if (address) {
        dispatch(SaveAddress(address))
        dispatch(SaveTronWeb(window.tronWeb))
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [userCode])

  const userAuthorizedOrNot = async () => {
    if (!window.tronWeb) {
      setLoadingText(t('app.tronlink.no'))
      return false
    }
    if (!window.tronLink) {
      setUserCode(200)
      dispatch(SaveTronWeb(window.tronWeb))
      Cookies.set('tronWeb_s', window.tronWeb)
      setTime(null)
      setTimes(null)
      return false
    }
    const res: requestAccountsResponse = await window.tronLink.request({
      method: 'tron_requestAccounts',
    })
    setTimeout(() => {
      if (res.code !== undefined) {
        Toast.show({
          content:
            i18n.language === 'zh'
              ? tronRequestAccountsCodeMsg.find((n) => n.value === res.code)
                  ?.lable
              : tronRequestAccountsCodeMsgEn.find((n) => n.value === res.code)
                  ?.lable,
        })
        setUserCode(res.code as number)
        setTime(null)
        setTimes(1000)
      }
    }, 500)
  }

  return (
    <AppWrapper>
      <Mask className="msgs" visible={visible}>
        <MaskContent>
          <div className="loadings">
            {loadingText}
            <Loading color="primary" />
          </div>
        </MaskContent>
      </Mask>
      {!visible && userCode === 200 && renderRoutes(routes)}
      {isLogin && <Login />}
    </AppWrapper>
  )
})
