import TronWeb from 'tronweb'
import { NetWorkAddress, privateKey, headers } from './constant'
declare const window: any

export const getTronAddress = () => {
  if (!window.tronWeb) {
    const HttpProvider = TronWeb.providers.HttpProvider
    const fullNode = new HttpProvider(NetWorkAddress)
    const solidityNode = new HttpProvider(NetWorkAddress)
    const eventServer = NetWorkAddress
    const tronWeb = new TronWeb(
      fullNode,
      solidityNode,
      eventServer,
      privateKey,
      headers,
    )
    window.tronWeb = tronWeb
  }
}

export const tronWebInit = {}
