import * as actionTypes from './constants'

const initState = {
  user: {},
  address: '',
  parentAddress: '410000000000000000000000000000000000000000',
}

export default function reducer(state = initState, action: any) {
  const { type } = action
  switch (type) {
    case actionTypes.SAVE_USER:
      return { ...state, user: action.user }
    case actionTypes.SAVE_ADDRESS:
      return { ...state, address: action.address }
    case actionTypes.SAVE_PARENT_ADDRESS:
      return { ...state, parentAddress: action.parentAddress }
    default:
      return state
  }
}
